.burger-menu {
  margin-left: unset;
  position: fixed;
  left: 260px;
}
.burger-menu::before {
  transform: translateY(10px) rotate(45deg);
  border: 2px solid white;
  background-color: white;
}
.burger-menu::after {
  transform: translateY(-10px) rotate(-45deg);
  border: 2px solid white;
  background-color: white;
}
