.header__wrapper {
  position: relative;
  height: 100%;
  max-width: 1200px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  margin-left: 5px;
  font-size: 6rem;
  text-shadow: 1px 1px white;
}
@media (max-width: 767px) {
  .header__logo {
    font-size: 3.5rem;
  }
}
@media (max-width: 500px) {
  .header__logo {
    display: none;
  }
}
.header__logo:hover {
  cursor: pointer;
}

.switch {
  position: relative;
  display: inline-block;
  margin-right: 40px;
  width: 120px;
  height: 44px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ff9100;
  -webkit-transition: 0.6s;
  transition: 0.3s;
  border-radius: 44px;
}
.slider::before {
  -webkit-transition: 0.6s;
  transition: 0.3s;
  position: absolute;
  content: "";
  height: 36px;
  width: 36px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  border-radius: 50%;
}
.slider p {
  color: white;
  position: relative;
  font-size: 2rem;
  margin: 10px;
  padding: 5px;
}
.slider .train {
  display: none;
}

input:checked + .slider, input :focus + .slider {
  background-color: #d400ff;
  justify-content: flex-start;
}
input:checked + .slider .play, input :focus + .slider .play {
  display: none;
}
input:checked + .slider .train, input :focus + .slider .train {
  display: unset;
}
input:checked + .slider:before {
  -webkit-transition: 0.6s;
  transition: 0.3s;
  -webkit-transform: translateX(76px);
  -ms-transform: translateX(76px);
  transform: translateX(76px);
}

.menu__list {
  position: fixed;
  display: flex;
  top: 0;
  left: -350px;
  z-index: 3;
  width: 320px;
  height: 600px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #d400ff;
}
.menu__list-menu {
  left: 0;
}
.menu__list a {
  font-size: 2rem;
  margin: 15px;
  text-decoration: none;
}
.menu__list li {
  font-size: 2rem;
  margin: 15px;
  text-decoration: none;
}
.menu__list li:hover {
  cursor: pointer;
  color: white;
}
.menu__list-play {
  background-color: #ff9100;
}

.menu__item {
  display: inline-flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
}
.menu__item img {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  margin-right: 10px;
}
.menu__item-active {
  color: white;
}

.main {
  margin: auto;
  margin-top: 10px;
  margin-bottom: 30px;
  max-width: 1200px;
  min-height: 75vh;
}
.main-over {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-over .img {
  height: 60vh;
  margin: auto;
}

.container__header {
  margin: 10px;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.category__title {
  min-width: 250px;
  font-size: 4rem;
}
@media (max-width: 500px) {
  .category__title {
    font-size: 2rem;
    min-width: 130px;
  }
}

.main__score {
  display: flex;
  margin: 10px;
  height: 60px;
  justify-content: flex-end;
  align-items: center;
  overflow: hidden;
}
.main__score img {
  margin: 10px;
  height: 90%;
}

.cards__container {
  max-width: 1200px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  perspective: 1500px;
}
@media (max-width: 767px) {
  .cards__container {
    perspective: 6000px;
  }
}

.category__card {
  display: flex;
  overflow: hidden;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  width: 280px;
  height: 280px;
  border-radius: 40px;
  background: white;
  border: 2px solid #d400ff;
}
.category__card img {
  width: 280px;
}
.category__card p {
  margin: auto;
}
.category__card:hover {
  filter: drop-shadow(0 0 0.75rem #ff008c);
  cursor: pointer;
  box-shadow: unset;
  transform: translate(1px, 2px);
  backdrop-filter: blur(20px);
}
.category__card-play {
  border: 2px solid #ff9100;
}
.category__card-play:hover {
  filter: drop-shadow(0 0 0.75rem #ff6600);
}

.card {
  width: 280px;
  height: 280px;
  position: relative;
  transition: transform 1s ease-in-out;
  transform-style: preserve-3d;
  transform-origin: center right;
  margin: 10px;
}

.word__card {
  position: absolute;
  display: flex;
  overflow: hidden;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  backface-visibility: hidden;
  width: 100%;
  height: 100%;
  border-radius: 40px;
  background: white;
  border: 2px solid #d400ff;
}
.word__card img {
  width: 100%;
}
.word__card p {
  margin: auto;
}
.word__card button {
  position: absolute;
  right: 15px;
  bottom: 25px;
  width: 50px;
  height: 50px;
  border: none;
  background-color: white;
}
.word__card button:hover {
  transform: scale(1.2);
}
.word__card button img {
  width: 100%;
}
.word__card-front:hover {
  filter: drop-shadow(0 0 0.75rem #ff008c);
  cursor: pointer;
}
.word__card-front.disabled {
  transform: translate(0px, 3px);
  filter: grayscale(80%);
}
.word__card-front.disabled:hover {
  filter: grayscale(80%);
  cursor: unset;
}
.word__card-back {
  transform: rotateY(180deg);
  text-align: center;
}
.word__card-play {
  border: 2px solid #ff9100;
}
.word__card-play img {
  width: unset;
  height: 100%;
  transform: translateX(-10%);
}
.word__card-play button {
  display: none;
}
.word__card-play:hover {
  filter: drop-shadow(0 0 0.75rem #ff6600);
}

.flipped {
  transform: rotateY(180deg);
  transform: translateX(-100%) rotateY(-180deg);
}

.play__button {
  display: none;
  width: 180px;
  height: 100px;
  border-radius: 20px;
  margin: 10px auto;
  border: 2px solid #ff9100;
}
.play__button-play {
  display: block;
  width: 80px;
  height: 80px;
  margin: 10px auto;
  border-radius: 50%;
}
.play__button-play img {
  height: 85%;
  width: 85%;
  margin: auto;
}
.play__button-play:hover {
  cursor: pointer;
  transform: scale(1.2);
  filter: drop-shadow(0 0 0.75rem #ff9100);
}
.play__button-start {
  display: block;
  width: 200px;
  border-radius: 20px;
}
.play__button-start::before {
  content: "Start game";
  font-size: 3rem;
  width: 100px;
  height: 20px;
}
.play__button-start img {
  height: 70%;
  width: 70%;
  margin: auto;
}

.footer {
  color: white;
  width: 100%;
  height: 10vh;
  min-height: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  background-color: #d400ff;
}
.footer p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 2rem;
  line-height: 160%;
  letter-spacing: unset;
  color: white;
}
.footer a {
  font-size: 2rem;
  text-decoration: none;
}
.footer-play {
  background-color: #ff9100;
}

.footer__school {
  display: flex;
  align-items: center;
}

.footer__logo {
  margin: 10px 5px;
  height: 30px;
}